export default {
	props: {
		value: {
			type: Boolean,
			default: true,
		},
		disabled: {
			type: Boolean,
			default: false,
		},
		activeColor: {
			type: String,
			default: '#0081ff'
		},
		inactiveColor: {
			type: String,
			default: '#fff'
		},
		fontSize: {
			type: Number,
			default: 40,
		},
	},
	data() {
		return {
			checked: this.value,
			switchbgColor: "",
			size: this.fontSize,
		}
	},
	computed: {
		classes() {
			return [
				`wa-switch`,
				`wa-switch-${this.checked}`,
				`wa-disabled-${this.disabled}`,
			]
		},
	},
	methods: {
		change() {
			if (this.disabled || this.loading) {
				return;
			} else {
				this.checked = !this.checked;
				this.$emit('change', this.checked);
				if (this.checked) {
					this.switchbgColor = this.activeColor;
				} else {
					this.switchbgColor = this.inactiveColor
				}
			}
		}
	},
	watch: {
		value(newValue) {
			this.checked = newValue;
			if (this.checked) {
				this.switchbgColor = this.activeColor;
			} else {
				this.switchbgColor = this.inactiveColor
			}
		},
	},
	mounted() {
		if (this.checked) {
			this.switchbgColor = this.activeColor;
		} else {
			this.switchbgColor = this.inactiveColor
		}

	}
}
